#boxs {
    z-index: -99999;
  position: relative;
  margin: 0 auto;
  height: 222.5px;
  width: 220px;
  /* display: grid; */
  background: #e5e5e5;
  box-sizing: border-box;
  clip-path: polygon(50% 0, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
  -webkit-clip-path: polygon(50% 0, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
  -moz-clip-path: polygon(50% 0, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
}
#heptagon {
  display: grid;
  place-items: center;
  position: absolute;
  left: 2px;
  right: 2px;
  top: 2px;
  bottom: 2px;
  border: 1px solid #e5e5e5;
  width: 216px;
  height: 219px;
  /* background-image: url('https://www.pcclean.io/wp-content/uploads/2020/4/NVvuim.jpg'); */
  background: #ffffff;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  clip-path: polygon(50% 0, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
  -webkit-clip-path: polygon(50% 0, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
  -moz-clip-path: polygon(50% 0, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
}