@font-face {
  font-family: "ocrFont";
  src: url("../Fonts/OCR.ttf") format("truetype");
}
@font-face {
  font-family: "Bahnschrift";
  src: url("../Fonts/BAHNSCHRIFT\ 1.TTF");
}
.MainLogo {
  font-family: "ocrFont";
}
.MainLogoThetaa {
  font-family: "Bahnschrift";
}
.MainMenu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  /* font-family: "ocrFont"; */
}

.Menu a:hover {
  color: darkmagenta;
}
/* connect btn */
.btnConnect {
  font-family: "Space Mono";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  background: #410ead;
  box-shadow: 0px 0px 5px rgba(65, 14, 173, 0.7);
  border-radius: 3px;
  color: white;
  padding: 5px 10px;
  cursor: pointer;
  z-index: 99999;
  position: relative;
}

.BgColor {
  background-color: rgb(184 252 195 / 30%);
}
.subMenuTitle {
  font-family: "Space Mono";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
}
.BgTab {
  background-color: #faf7ff;
}
.BgSubTitle {
  background-color: #faf7ff;
}
/* end connect btn */
.menuTitle {
  font-family: "Space Mono";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  /*color: #222222;*/
}

.Menu {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 30px;
}

.MenuList:hover {
  /* title hover */
  color: #222222;
  background: white;
  /*padding-top: 100px;*/
}
.tabHover:hover {
  background-color: #b8fcc3;
  cursor: pointer;
}

.dropdownMenu {
  top: 60px;
  min-width: 100%;
  background: white;
  /*display: none;*/
  position: absolute;
  z-index: 9999 !important;
  left: 0;
  height: auto;
  padding: 30px 10px 5px;
  border-bottom: #666 solid;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.dropsub:hover ul.dropdownMenu {
  opacity: 1;
  visibility: visible;
}
.dropsub ul.dropdownMenu {
  opacity: 1;
  visibility: visible;
}
.dropsub ul.dropdownMenu {
  opacity: 0;
  visibility: hidden;
}

.MoreTitle {
  font-family: "Space Mono";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
}

.sumTitlePre {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  color: #59587b;
}

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color: #faf7ff;
  /*background-color: rgb(244 239 255);*/
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidenav a {
  /*padding: 8px 8px 8px 32px;*/
  text-decoration: none;
  font-size: 20px;
  color: #222222;
  display: block;
  transition: 0.3s;
}

.sidenav a:hover {
  color: #222222;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 96px;
  font-size: 40px;
  margin-left: 50px;
}
.mobileView {
  display: none;
}
@media screen and (max-width: 1024px) {
  .MainMenu {
    display: none;
  }
  .mobileView {
    display: block;
    padding: 25px;
  }
  .sidenav a {
    font-size: 16px;
  }
}
@media screen and (max-width: 480px) {
  .sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 20px;
    font-size: 40px;
    margin-left: 50px;
  }
}
@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }
  .sidenav a {
    font-size: 18px;
  }
}

.dropdown-btn {
  font-family: "Space Mono";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  /* identical to box height */
  text-transform: capitalize;
  color: #222222;
}
.dropdown-btn:focus {
  outline: 0;
}

.dropdown-menu {
  position: sticky;
  top: 30px;

  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  max-height: 0;
  width: 100%;
  -webkit-transition: max-height, 0.8s ease-in-out;
  transition: max-height, 0.8s ease-in-out;
  overflow-y: hidden;
  overflow-x: hidden;
}
.display-menu {
  max-height: 100vh;
}
.menu-link {
  /*color: #444;*/
  text-decoration: none;
  font-size: 1.4rem;
  width: 100%;
  padding: 0px 0 20px 30px;
}

.menu-link:focus {
  outline: 0;
}

.cover {
  height: max-content;
  background-color: #c5c5c5;
}
