@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Allerta+Stencil&display=swap");

* {
  font-family: "Inter";
  font-style: normal;
}

a {
  text-decoration: none;
  color: #222222;
}

ul li {
  list-style: none;
}
input:focus {
  outline: none;
}
.monospace {
  font-family: "Space Mono";
}
.btn {
  background-color: #0e031f;
  color: #fff;
  border-radius: 1000px;
  padding: 16px 32px;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  box-shadow: 4px 0 #f15a24;
  transition: all 0.2s ease;
}
.btn:hover {
  background-color: #f15a24;
  box-shadow: 0 0 #f15a24;
  transform: translate(4px);
}
.link:hover {
  padding-left: 0;
  padding-right: 48px;
  color: #f15a24;
  position: inherit;
}
.link {
  padding-left: 48px;
  padding-right: 0;
  position: relative;
  display: inline-block;
  transition: all 0.2s ease;
  color: #0e031f;
  font-size: 18px;
  line-height: 140%;
  font-weight: 500;
}

.link:before {
  position: absolute;
  content: "";
  left: 0;
  top: calc(50% - 1px);
  width: 40px;
  height: 2px;
  background-color: #f15a24;
  opacity: 1;
  transition: opacity 0.2s ease;
}
.link:after {
  opacity: 0;
  position: absolute;
  right: 16px;
  top: calc(50% - 12px);
  content: "";
  width: 24px;
  height: 14px;
  background-image: url("./Common/Img/About/right-arrow.svg");
  transition: opacity 0.2s ease;
}
.Accordiondata button {
  display: flex !important;

  justify-content: space-between !important;

  padding: 15px 30px;
}
.BlockChanImg img {
  display: initial;
}

.small {
  min-height: 20px;
  max-height: 20px;
  overflow: hidden;
  transition: 0.5s;
}
.full {
  min-height: 175px;
  max-height: 100%;
  transition: 0.5s;
  overflow: hidden;
}

.small:hover {
  min-height: 175px;
  max-height: 100%;
  transition: 0.5s;
  overflow: hidden;
}
.icon:hover {
  fill: #da4567;
}
