@media screen and (max-width: 480px) {
  .btn {
    margin: 0 20px;
  }
  .Accordiondata button {
    padding: 5px 10px;
  }
}
@media screen and (max-width: 375px) {
  .btn {
    padding: 7px 6px;
    margin: 0 10px;
  }
}
