.main-boat {
    width: 100vw;
}

.boat {
    height: 100px;
    width: 150px;
    animation: moveToLeft 60s linear infinite;
  
  }

  .inner-boat {
    animation: boat 3s alternate infinite;
  }

  .ocean {
    min-width: 100%;
    height: 10px;
    animation: ocean 7s alternate infinite;
  }

  @keyframes moveToLeft {
    0% {
      transform: translateX(-250px);
    }
    100% {
        transform:translateX(1750px);
    }
  } 
  
  @keyframes boat {
    from {
      transform: rotate(-3deg);
    }
    to {
      transform: rotate(3deg);
    }
  }

  @keyframes ocean {
    from {
      transform: rotate(1deg);
    }
    to {
      transform: rotate(-1deg);
    }
  }
